@import ../../../defaultStyle/variables
    
.menuPC
    display: flex
    min-width: 45rem
    
    justify-content: space-between
    position: relative
    text-transform: uppercase
    font-size: 1.3rem       
    a
        color: $text-icons
        &:hover
            color: $backlight-text-icons
    
    
.subMenu
    position: absolute
    padding-top: 1rem
    ul
        display: flex
        flex-direction: column
        justify-content: space-between
        background: $trancparent-color
        padding: 1rem
        box-shadow: 2px 2px 15px #000 
        border-radius: 0.5rem
        min-width: 12rem
        li
            margin-top: 1rem
            

a.link
    color: $backlight-text-icons

.Span
    color: $text-icons
    cursor: pointer
    &:hover
            color: $backlight-text-icons
.activeSpan
    color: $backlight-text-icons

            
            

            